.Home {
  margin: 160px 0 0 0;
  border-bottom: 1px solid black;
}

h1 {
  text-align: center;
  font-size: 4em;
  letter-spacing: 2px;
  text-shadow: 4px 4px 8px rgb(58, 58, 58);
  padding: 2rem;
}

.logoSteel img {
  display: block;
  width: 60%;
  margin: auto;
}

.welcomeText {
  padding: 1rem;
  margin: auto;
  width: 70%;
  background: rgba(17, 17, 17, 0.3);
  font-size: 20px;
  text-align: center;
}

.video {
  display: block;
  margin: 2rem auto 2rem auto;
  width: 68%;
}
