header {
  background: whitesmoke;
  height: 4rem;
  padding: 0.5rem 1rem;
  position: fixed;
  top: 0;
  width: 100%;

  display: flex;
  align-items: baseline;
  justify-content: space-between;
  z-index: 1;

  font-size: 18px;
}

img {
  display: block;
  height: 50%;
  width: 50%;
}

nav {
  font-size: 1.2em;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.link {
  padding: 16px;
  margin: 0 64px;
}

/* .link a {
  color: black;
  text-decoration: none;
  padding: 2px;
} */

.link a:after {
  content: "";
  display: block;
  width: 0;
  height: 1px;
  background: whitesmoke;
  transition: width 0.2s ease;
}

.link:hover {
  cursor: pointer;
}

.link a:hover:after {
  width: 100%;
  left: 0;
  background: black;
}
