.tickets {
  background: whitesmoke;
  color: #111;
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
  margin-top: 24px;

  font-family: monospace;
  font-size: 16px;
}

form {
  border: 1px solid #111;
  margin: 8px;
  width: 120%;
}

.ticketsH3 {
  color: black;
  text-align: center;
  margin: 16px;
  font-size: 32px;
  font-weight: 100;
  font-family: "Big Shoulders Display", cursive;
}

.formInfo {
  color: red;
  font-weight: bold;
  padding: 6px;
}

.names,
.genders,
.numOfTickets,
.dateOfArrival,
.email {
  margin: 8px 16px;
}

.gender {
  margin: 4px;
}

.agreement {
  margin: 32px 16px 16px 24px;
}

.paymentButton {
  margin: 8px;
  padding: 4px;
}

.names input,
.email input {
  padding: 1px;
}

.contact h4 {
  padding: 4px;
}

.contact p {
  padding: 2px 16px;
}
