footer {
  background: whitesmoke;
  font-family: "Big Shoulders Display", cursive;
  border-top: 2px solid whitesmoke;

  padding: 1rem 1rem;
  height: 6rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.parks {
  margin-left: 80px;
  font-size: 1.5rem;
  letter-spacing: 2px;
  color: #111;
}

.footerParks {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 8px;
}

.footerLogo {
  display: block;
  width: 4%;
  height: 4%;
  padding: 12px;
  transition: 0.2s;
  transition-delay: 0.1s;
}

.footerLogo:hover {
  padding: 6px;
  cursor: pointer;
}
